h2 {
  text-align: center;
}

.left-container {
  background-color: var(--app-color-secondary);
  background:
    /* Main gradient from top to bottom */ linear-gradient(
      to bottom,
      transparent 0%,
      transparent 97%,
      var(--app-color-secondary) 100%
    ),
    /* Bottom left - secondary color */
      radial-gradient(
        circle at 85% 15%,
        var(--app-color-secondary) 30%,
        transparent 100%
      ),
    /* Bottom right - accent color */
      radial-gradient(
        circle at 80% 80%,
        var(--app-color-accent) 0%,
        transparent 45%
      );
  background-blend-mode: multiply;
  background-size: 100% 100%;
}

.glass-like {
  position: relative;
  backdrop-filter: blur(12px);
  -webkit-backdrop-filter: blur(12px);
  background: linear-gradient(
    135deg,
    rgba(255, 255, 255, 0.1) 0%,
    rgba(var(--app-color-accent-rgb, 59, 130, 246), 0.15) 100%
  );
  border: 1px solid rgba(255, 255, 255, 0.1);
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1),
    inset 0 0 1px 1px rgba(255, 255, 255, 0.05);

  /* Fallback for browsers that don't support backdrop-filter */
  @supports not (backdrop-filter: blur(12px)) {
    background: rgba(255, 255, 255, 0.08);
  }

  &::before {
    content: "";
    position: absolute;
    inset: 0;
    background: radial-gradient(
      circle at 20% 20%,
      rgba(255, 255, 255, 0.3) 0%,
      transparent 50%
    );
    opacity: 0.5;
    pointer-events: none;
  }
}
